.cover {
  width: 100%;
  height: 100vh;
  background: rgba(2, 19, 70, 0.753);
  align-items: center;
  display: flex;
  .container {
    max-width: 800px;
    width: 80%;
    margin: 0 auto;
    color: rgb(226, 226, 226);
    text-align: center;
    img {
      width: 180px;
      height: 180px;
      margin: 0 auto;
      border-radius: 50%;
      opacity: 0.8;
      transition: 0.3s;
      border: 5px solid rgba(245, 245, 245, 0);
      animation: rotationX-15 5s ease infinite;
    }
    img:hover {
      // transform: scale(1.2);
      cursor: pointer;
      border: 3px solid white;
      border-radius: 50%;
    }
    h1 {
      cursor: pointer;
      font-size: 2em;
      letter-spacing: 4px;
      padding: 30px;
    }
    .sub-container {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      a {
        color: rgb(250, 250, 250);
        padding: 10px 12px;
        border-radius: 5px;
        text-decoration: none;
        border: 1px solid rgba(255, 217, 0, 0);
      }
      a:hover {
        border: 1px solid whitesmoke;
      }
    }
  }
}

@media screen and (max-width: 470px) {
  .sub-container a {
    display: none;
  }
}
